.tab {
    margin-bottom: 20px;
    width: 230px
}

.tab:hover {
    color: #E91E63 !important
}
.linkItem {
    color: white;
    font-weight: 500;
    font-size: 24px;
    padding: 30px 105px;
    cursor: pointer;
    border: 2px solid transparent;
    border-radius: 5px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.section-1:hover .linkItem,
.section-1:active .linkItem,
.section-1:focus .linkItem {
    background-color: #514f4f;
    border-bottom: 2px solid crimson;
    outline: none;

    color: white;
}

.linkItem:hover,
.linkItem:active,
.linkItem:focus {
    background-color: #514f4f;
    border-bottom: 2px solid crimson;
    outline: none;
}       
.nav-item {
    padding: 20px 8px;
    margin: 20px 43px;
    text-align: center;
    border-radius: 5px;
    font-size: 16px;
    color: white;
    position: static
}

.nav-item.active {
    color: #000000!important;
    background: white;
}

.nav-item:hover {
    color: #000;
    background: #fff;
}

.nav-link { 
    font-weight: 700;
    color: rgb(214 21 44)!important;
}   

.nav-link:hover {
    color: black!important;
}  
